import React, { Component } from 'react'
import { ProductConsumer } from '../context'

export default class ListGroup extends Component {
  render() {
    const arr = ['All', 'Phone', 'Tab', 'Headphones'];

    return (
      <ProductConsumer>
        {value => (

          < ul className="list-group">
            {arr.map((item, i) => {
              return (
                <li
                  onClick={() => value.selectGroup({ item })}
                  key={i}
                  className={value.selectedTypeGroup.toLowerCase() === item.toLowerCase()
                    ? 'list-group-item active'
                    : 'list-group-item'
                  }
                >
                  {item}
                </li>
              )
            })}
          </ul>
        )
        }
      </ProductConsumer>
    )
  }
}

