import React, { Component } from 'react'
import Product from './Product'
import Title from './Title'
import ListGroup from './ListGroup'
import { ProductConsumer } from '../context'

export default class ProductList extends Component {


  render() {

    let currentLength = []

    return (
      <>
        <div className="py-4">
          <div className="container">
            <div className="row">
              <div className="col">
                <Title name="our" title="products" />
              </div>
            </div>


            <div className="row">

              <div className="col-lg-2">
                <ListGroup />
              </div>

              <div className="col-lg-10">
                <div className="row">
                  <ProductConsumer>
                    {value => {

                      let products = [...value.products]

                      if (value.selectedTypeGroup !== 'All') {
                        products = products.filter(it => {
                          if (it.type.toLowerCase() === value.selectedTypeGroup.toLowerCase()) return it
                        })
                      }

                      currentLength = [...products]

                      products = products.filter((it, i) => {
                        if (i > value.currentPage * 9 - 9 && i <= value.currentPage * 9) return it
                      })

                      return products.map(product => {
                        return <Product
                          key={product.id}
                          product={product}
                          currentPage={value.currentPage}
                          currentType={value.selectedTypeGroup}
                        />
                      })
                    }}
                  </ProductConsumer>
                </div>
              </div>
            </div>

            <div className="row">
              <div className="col">
                <nav>
                  <div className='pagination'>
                    <ProductConsumer>
                      {value => {
                        const arr = [];
                        for (let i = 0; i < Math.ceil(currentLength.length / 9); i++) arr.push(i);

                        return arr.map((it, i) => {
                          i++;
                          return (
                            <div key={i}>
                              <div
                                className={value.currentPage === i ? 'page-item active' : 'page-item'}
                                onClick={() => value.onPage(i)}
                              >
                                <a className="page-link">{i}</a>
                              </div>
                            </div >
                          )
                        })
                      }}
                    </ProductConsumer>
                  </div>
                </nav>
              </div>
            </div>

          </div>
        </div>
      </>
    )
  }
}


