import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import { ProductConsumer } from '../context'
import PropTypes from 'prop-types'

export default class Product extends Component {
  render() {
    const { id, title, type, img, price, inCart, } = this.props.product;


    return (

      <div className='col-9 mx-auto col-md-6 col-lg-4 my-3 product'>
        < div className="card" >

          <ProductConsumer>
            {value => (

              <div className="img-container p-5 " onClick={() => value.handleDetail(id)}>
                <Link to='/details'>
                  <img
                    src={img}
                    alt="product"
                    className={type !== 'tab' ? 'card-img-top' : 'card-img-top card-img-top-tab'}
                  />
                </Link>

                <button
                  className="cart-btn"
                  disabled={inCart ? true : false}
                  onClick={() => {
                    value.addToCart(id);
                    value.openModal(id);
                  }}
                >

                  {inCart ? (
                    <Link to='/cart' className="inCart">
                      <p className="text-capitlize mb-0" disabled>
                        {" "}
                        Cart
                      </p>
                    </Link>
                  ) : (<i className="fas fa-cart-plus"></i>)}

                </button>
              </div>
            )}
          </ProductConsumer>

          {/* card footer */}

          <div className="card-footer d-flex justify-content-between">
            <p className="align-self-center mb-0">{title}</p>
            <h4 className="text-blue font-italic mb-0">
              <span className="mr-1">$</span>
              {price}
            </h4>
          </div>
        </div >
      </div >
    )
  }
}

Product.propTypes = {
  product: PropTypes.shape({
    id: PropTypes.number,
    img: PropTypes.string,
    title: PropTypes.string,
    price: PropTypes.number,
    inCart: PropTypes.bool,
  }).isRequired
}